<template>
	<view class="wrapper">
		<!-- <view class="add" @click="add">
			<text>新增录入信息</text>
			<image src="../../static/add.png"></image>
		</view> -->
		<view class="list_w">
			<scroll-view
				class="scroll_view"
				:scroll-y="true" 
				:scroll-with-animation="true" 
				:enable-back-to-top="true"
				@scrolltolower="scrollBottom"
			>
				<view class="list"  v-if="!empty">
					<view class="item" v-for="(item,index) in listData" :key="index">
						<view class="info">
							<!-- <image src="../../static/award.png" class="avatar"></image> -->
							<view class='other'>
								<view class='name_w'>
									<text class="name">{{item.name}}</text>
								</view>
								<view class='tel'>{{item.mobile}}</view>
							</view>
						</view>
						<view class="go" @click="goDesc(item)">
							<view class="btn">
								<button class="cu-btn luru">
									信息录入
								</button>
							</view>
						</view>
					</view>
					<loadMore :status="status" :content-text="textDesc"></loadMore>
				</view>
				<view class="no_data" v-else>
					暂无数据
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"loading", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				pageInfo:{
					pageNum:1,//当前页
					pageSize:10//每页多少条
				},
				flag:false,
				empty:false,
				listData:[],
			};
		},
		methods:{
			goDesc(item){
				this.perfectNavigateTo({
					url:"/pages/staff/createOrder?fromType=1&info="+JSON.stringify(item)
				})
			},
			scrollBottom(){
				if(this.status=="more"){
					this.getList()
				}
			},
			getList(){
				this.request("/staff/bindlist",this.pageInfo).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data.list.length>0){ //如果有数据
							this.empty=false
							this.listData=this.listData.concat(_data.list)
							if(_data.list.length<this.pageInfo.pageSize){ //如果有数据但是 小于 每页数量
								this.status="noMore"
							}else{
								this.pageInfo.pageNum++
								this.status="more"
							}
						}else{ //如果这次没数据
							this.empty=false
							this.status="noMore"
							if(this.pageInfo.pageNum==1){ //说明开始就没数据
								this.empty=true
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
		},
		onShow(){
			this.listData=[]
			this.getList()
		}
	}
</script>

<style lang="stylus" scoped>
	.wrapper
		height:98vh
		padding:0upx 30upx
		.add
			height:112upx;
			display:flex;
			flex-direction:row;
			align-items center;
			background:rgba(248,249,252,1);
			border-radius:16upx;
			padding:0upx 30upx
			justify-content space-between
			margin-top:30upx
			text
				font-size:32upx;
				font-family:PingFangSC-Semibold,PingFang SC;
				font-weight:600;
				color:rgba(30,30,30,1);
			image
				width:28upx
				height:28upx
		.list_w
			.scroll_view
				height:98vh
			.list
				border:1upx solid transparent
				.item
					display:flex
					flex-direction row
					justify-content space-between
					align-items center
					margin-top:76upx
					&:first-child
						margin-top:46upx
					.info
						display:flex
						flex-direction row
						align-items center
						.avatar
							width:108upx
							height:108upx
							border-radius 50%
							overflow hidden
							margin-right:40upx
						.other
							.name_w
								display:flex
								flex-direction row
								align-items center
								margin-bottom:10upx
								.name
									font-size:32upx;
									font-family:PingFangSC-Semibold,PingFang SC;
									font-weight:600;
									color:rgba(30,30,30,1);
									margin-right:20upx
								.status
									height:30upx;
									line-height 30upx
									text-align center
									border-radius:4upx;
									padding:5upx 10upx
									font-size:24upx
									color:#fff
									&.unpass
										background:rgba(112,208,151,1);
									&.pass
										background:#B2B6C0
					.go
						.luru
							background:#1F2334
							color:#fff
							letter-spacing 2upx						
</style>
